<script>
/* eslint-disable */

import { formatNum, toDec, fromDec } from "./../decUtil";
import "element-ui/lib/theme-chalk/index.css";
import Proxy from "./../proxy.js";
import { ZERO_ADDR } from "../constants.js";

export default {
  name: "luckyStar",
  data() {
    return {
      lucky_addr: ZERO_ADDR,
      lucky_url: null,
      lucky_date: "00/00/0000 00:00:00",
      lucky_bonus: "0",

    };
  },

  created() {
    Proxy.initWeb3Account((web3, acc) => {
      this.account = acc;
      this.web3 = web3;
      this.init();
    });
  },

  methods: {
    async init() {
      this.ethlife = await Proxy.getEthLife();

      this.chainId = await this.web3.eth.net.getId()
      console.log("chainId:" +  this.chainId)

      this.getLucky();
    },

    formatTstoDate(ts) {
      var joinTime = new Date(ts * 1000);
      return (
        joinTime.toLocaleDateString() +
        " " +
        joinTime.toTimeString().substr(0, 8)
      );
    },

    getLucky() {
      this.ethlife.getLucky().then((r) => {
        // ucky.inValue, lucky.luckyValue, lucky.ts, lucky.user
        if(r[2].toNumber() > 0) {
          this.lucky_bonus = formatNum(this.web3.utils.fromWei(r[1]), 2);
          this.lucky_date = this.formatTstoDate(r[2].toNumber());
          this.lucky_addr =
            r[3].substring(0, 12) + "..." + r[3].substring(30, 42);
          if(this.chainId == "5") {
            this.lucky_url = "https://goerli.etherscan.io/address/" + r[3];
          } else {
            this.lucky_url = "https://etherscan.io/address/" + r[3];
          }
          
        }

      });
    },
  },
};
</script>


